.content {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;

  .formulario {
    position: relative;
    height: 75%;
    width: 85%;
    background-color: rgba(0, 0, 0, 0.568);
    display: inline-flex;

    .info {
      width: 50%;
      height: 100%;
      .title {
        width: 40%;
        color: rgb(247, 241, 241);
        border-bottom: 4px solid white;
        text-indent: 20%;
      }
      .text {
        color: white;
        font-size: 1.5rem;
        width: 80%;
        height: 70%;
        line-height: 40px;
        word-spacing: 5px;
        display: flex;
        align-items: center;
        margin: auto;
        overflow-y: auto;
        overflow-x: hidden;
      }
    }
    .form {
      width: 50%;
      height: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    
      .form-group {
        margin-top: 20px;
        label {
          color: white;
          font-weight: bold;
          font-size: 22px;
          
        }

        .textArea {
          resize: none;
          height: 250px;
          width: 100%;
          border-radius: 22px;
          border: none;
          font-weight: bold;
          font-size: 1rem;
          text-indent: 22px;
          margin-top: 5px;
        }
        .textArea:focus {
          outline: 0 none;
        }
      }

      .Email {
        width: 100%;
        height: 40px;
        border-radius: 20px;
        border: none;
        outline: none;
        font-weight: bold;
        font-size: 1rem;
        text-indent: 22px;
        margin-top: 5px;

        input:focus {
          outline: none;
          border: none;
        }
      }
      .submit {
        width: 100%;
        height: 40px;
        border-radius: 20px;
        border: none;
        outline: none;
        font-weight: bold;
        font-size: 1.5rem;
        text-indent: 22px;
        background: #393d3f;
        box-shadow: 4px 4px 2px rgba(0, 0, 0, 0.55);
        color: rgb(255, 255, 255);
        font-weight: bold;
        font-size: 18px;
        margin-top: -20px;
      }
    }
  }
}
