.content {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;

  .aside {
    width: 20%;
    height: 75%;
    background-color: rgba(0, 0, 0, 0.568);


    .title {
        width: 100%;
        font-size: 0.8rem;
        display: flex;
        align-items: center;
        justify-content: center;
        color: rgb(247, 241, 241);
        border-bottom: 4px solid white;
        padding-bottom: 22px;
    }
    .options {
      border-color: 20px;
      width: 100%;
      height: 80%;
      display: flex;
      align-items: center;
      justify-content: center;
      ul {
        list-style: none;
        padding: 0;
        height: 100%;
        li {
          margin-top: 30%;
          color: white;
          border-bottom: 2px solid white;
          font-size: 0.6em;
          a:link,
          a:visited,
          a:active {
            text-decoration: none;
            color: white;
            font-size: 22px;
            font-weight: bolder;
          }
        }
      }
    }
  }
  .Guide {
    width: 60%;
    height: 75%;
    background-color: rgba(0, 0, 0, 0.568);
    position: relative;
    margin-left: 20px;
    display: block;
    overflow-y: scroll;
    scroll-behavior: smooth;

    .Option {
      width: 100%;
      height: auto;
      margin: 0;
      .title {
        padding-top:2%;
        width: 50%;
        margin-bottom: 2%;
        color: rgb(247, 241, 241);
        border-bottom: 4px solid white;
        text-indent: 20%;
      }
      .text {
        left: 20px;
        width: 95%;
        margin: 0 auto;
        font-size: 1.3rem;
        color: white;
        line-height: 40px;
        word-spacing: 5px;
        p {
          width: 100%;
          font-size: 0.9em;
          font-weight: bold;
        }
      }
    }
  }
}
