.content {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  
  .searchFood {
    height: 30%;
    width: 55%;
    background-color: rgba(0, 0, 0, 0.568);
  
    h2,
    h4 {
      color: white;
      font-weight: bold;
    }
    h2 {
      padding-top: 10px;
     
      margin-left:20px;
    }
    h4 {
      margin-left:40px;
      font-size: 18px;
      
    }
    .searchTool {
      width: 70%;
      height: 50px;
      display: flex;
      margin: auto;
      .search {
        width: 100%;
        height: 100%;
        border-radius: 24px 24px 24px 24px;
        border: none;
        outline: 0px;
        font-size: large;
        font-weight: bold;
        text-align: center;
      }
      .bi {
        position: absolute;
        color: rgb(75, 75, 78) ;
        right:75%;
        top:55%;
        width: 70px;
        height: 50px;
        
     
      }
}
.list{
 background-color: rgba(0, 0, 0, 0.568);
 overflow-y: scroll;
 overflow-x: hidden;
 margin: auto;
 margin-top:10px;
 width: 100%;
 height:auto;
 max-height: 100%;
 margin-top: 2.1%;
}
.food{
 width: 428px;
 height: 25%;
 margin:  auto;
 padding: 0;
 padding-top: 22px;
 text-align: center;

 a,a:focus,a:visited,a:hover{
   text-decoration: none;
   color:rgb(253, 253, 253);
   font-weight: bold;
   font-size:1.2em;
   height: 1200px;
   text-transform: uppercase;
}
   }
  }
}
