.background-food {
  height: 100vh;
  width: 100vw;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(0, 0, 0, 0.568);

 
 

  .details-food {
    //margin-top: 10%;
    position: fixed;
    top:60px;
    overflow-y: scroll;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(400px,1fr));

    .element {
      padding:15px;
      width: 400px;
      height: auto;
      border: 2px double white;
      margin:0 auto;
      margin-top:20px;
      text-align: center;
      

      animation: big 2s  ;

      @keyframes big {
        from { transform: scale(0); }  
        to { transform: scale(1); }  
      }


      .composition-title-food {
         
        color: white;
       
        h1{
            border-bottom: rgb(247, 245, 245) 2px solid;
           
        }
      }
      .composition-value-food {

        font-size:44px;
        
          p{
            color: rgb(255, 255, 255);
          }
        
      }
    }
  }
  
}
